.HeaderItem_block{
    width: auto;
    height: 70px;
    margin: 0px 20px 0px 0px;
}
.HeaderItem_block:hover {
    background: #f0f0f0;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}
.HeaderItem_Link{
    width: auto;
    padding: 20px 0px 9px;
    position: relative;
    
}
.HeaderItem_Link > a {
    text-decoration: none;
}
.HeaderItem_heading{
    color: black;
    font-weight: 500;
    text-align: start;
    font-size: 1rem;
}
.HeaderItem_dec{
    font-size: 0.9rem;
    color: #888;

}
@media (max-width: 1296px){
    .HeaderItem_dec {
        display: none;
    }
}
.HeaderItem_active::after{
    content: "";
    width: 100%;
    height: 4px;
    border-radius: 4px;
    background: rgb(85,26,139);
    position: absolute;
    bottom: 5px;
    left: 0px;
}